<template>
    <div>
        <a href="#" id="export" @click="exporta">export</a>
        <button type="button" class="btn btn-sm btn-style9  text-xs" @click="modes=!modes" style="position:fixed;top:10px;left:100px;z-index:1000;" id="modesnya">Mode</button>
        <button type="button" class="btn btn-sm btn-style9  text-xs" @click="setting=!setting" style="position:fixed;top:10px;left:200px;z-index:1000;" >Setting</button>
        <div class="sm-form p-3" v-if="setting" style="position:fixed;top:60px;left:200px;z-index:1000;background:white;">
            <label for="color">Color</label>
            <input type="color" id="color" name="color" class="form-control form-control-sm" placeholder="color" v-model="vdata2.color" >
            <label for="title">title</label>
            <input type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata2.title" >
        </div>
        <button type="button" class="btn btn-sm btn-style9  text-xs" @click="$router.go(-1)" style="position:fixed;top:10px;right:50px;z-index:1000;">{{$route.query.nama}}</button>
        <div v-if="modal" style="height:100vh;width:100vw;position:fixed;z-index:100;background:rgba(151,151,179,0.4);" class="d-flex justify-content-center align-items-center " @click="modal=false">
            <div style="width:100%">
            </div>
        </div>
        <div v-if="modal" style="position: fixed;top: 50%;left: 50%;transform: translateX(-50%) translateY(-50%);z-index:1000;">
            <div style="height:100px;" class="d-flex justify-content-center align-items-center ">
                <div style="" class="w-full">
                    <div class="p-4 bg-white rounded-lg shadow">
                        <p class="text-xs text-center font-bold p-3 uppercase">Pilih Kategori </p>
                            <button type="button" @click="pilihkategori='icon'" :class="`${pilihkategori=='icon'?'btn-dark':'btn-outline-dark'}`" class="btn btn-sm ml-2">Icon</button>
                            <button type="button" @click="pilihkategori='barcode'" :class="`${pilihkategori=='barcode'?'btn-dark':'btn-outline-dark'}`" class="btn btn-sm ml-2">Barcode</button>
                            <button type="button" @click="pilihkategori='gambar'" :class="`${pilihkategori=='gambar'?'btn-dark':'btn-outline-dark'}`" class="btn btn-sm  ml-2">Gambar</button>
                            <button type="button" @click="pilihkategori='radius'" :class="`${pilihkategori=='radius'?'btn-dark':'btn-outline-dark'}`" class="btn btn-sm  ml-2">Radius</button>
                            <!-- <button type="button" @click="pilihkategori='floorplan'" :class="`${pilihkategori=='radius'?'btn-dark':'btn-outline-dark'}`" class="btn btn-sm  ml-2">Floor Plan</button> -->
                        <div style="width:70vw;height:80vh;overflow:scroll;">
                            <form class="text-sm" action="" @submit.prevent="submit">
                                <div v-if="pilihkategori=='floorplan'">
                                    <p class="text-cneter">Tambahkan maximum/minimum 1 floor plan</p>
                                    <div class="sm-form ">
                                        <label for="floorplan">Floor plan</label>
                                        <input type="file" id="filenya3" name="floorplan" class="form-control form-control-sm" placeholder="floorplan" @change="upload3">
                                        <input type="text" id="floorplan" name="floorplan" class="form-control form-control-sm" placeholder="floorplan" v-model="vdata.floorplan" >
                                    </div>
                                    <img :src="vdata.floorplan" style="height:70px;" v-viewer v-if="vdata.floorplan" alt="">
                                    <div class="sm-form ">
                                        <label for="link3d">Link 3D</label>
                                        <input type="text" id="link3d" name="link3d" class="form-control form-control-sm" placeholder="link3d" v-model="vdata.link3d" >
                                    </div>
                                    <div class="sm-form ">
                                        <label for="nilai1">Your nilai1</label>
                                        <input type="text" id="nilai1" name="nilai1" class="form-control form-control-sm" placeholder="nilai1" v-model="vdata.nilai1" >
                                    </div>
                                    <div class="sm-form ">
                                        <label for="nilai2">Your nilai2</label>
                                        <input type="text" id="nilai2" name="nilai2" class="form-control form-control-sm" placeholder="nilai2" v-model="vdata.nilai2" >
                                    </div>
                                    <div class="sm-form ">
                                        <label for="nilai3">Your nilai3</label>
                                        <input type="text" id="nilai3" name="nilai3" class="form-control form-control-sm" placeholder="nilai3" v-model="vdata.nilai3" >
                                    </div>
                                    <div class="sm-form ">
                                        <label for="nilai4">Your nilai4</label>
                                        <input type="text" id="nilai4" name="nilai4" class="form-control form-control-sm" placeholder="nilai4" v-model="vdata.nilai4" >
                                    </div>
                                    <div class="sm-form ">
                                        <label for="center1">Your center1</label>
                                        <input type="text" id="center1" name="center1" class="form-control form-control-sm" placeholder="center1" v-model="vdata.center1" >
                                    </div>
                                    <div class="sm-form ">
                                        <label for="center2">Your center2</label>
                                        <input type="text" id="center2" name="center2" class="form-control form-control-sm" placeholder="center2" v-model="vdata.center2" >
                                    </div>
                                </div>
                                <div v-else class="sm-form ">
                                    <div class="row justify-content-center">
                                        <div class="col-sm-5">
                                            <!-- RADIUS -->
                                            <div v-if="pilihkategori=='radius'">
                                                <p>Wilayah/Kecamatan : </p>
                                                <v-select :options="wilayah" label="kecamatan" v-model="vdata.wilayah" :reduce="e => e.id"></v-select>
                                            </div>
                                            <!-- RADIUS -->
                                            <!-- ALL -->
                                            <div class="sm-form " v-if="pilihkategori!='barcode'">
                                                <label class="p-0 mt-2" for="title">Title : </label>
                                                <input required type="text" id="title" name="title" class="form-control form-control-sm" placeholder="title" v-model="vdata.title" >
                                            </div>
                                            <div v-else>
                                                <div class="sm-form ">
                                                    <label for="idbarcode">ID Barcode</label>
                                                    <input type="text" id="idbarcode" name="idbarcode" class="form-control form-control-sm" placeholder="idbarcode" v-model="vdata.idbarcode" >
                                                </div>
                                            </div>
                                            <p class="text-sm">Deskripsi : </p>
                                            <div class="sm-form">
                                            <textarea type="text" id="deskripsi" name="deskripsi" rows="2" placeholder="deskripsi..." class="form-control md-textarea" v-model="vdata.deskripsi"
                                            ></textarea>
                                            </div>
                                            <div class="row">
                                                <div class="col-6">
                                                    <div class="sm-form ">
                                                        <label class="text-xs mt-1 p-0" for="lat">Lat : </label>
                                                        <input type="text" id="lat" name="lat" class="form-control form-control-sm" placeholder="lat" v-model="vdata.lat" >
                                                    </div>
                                                </div>
                                                <div class="col-6">
                                                    <div class="sm-form ">
                                                        <label class="text-xs mt-1 p-0" for="lng">Lng : </label>
                                                        <input type="text" id="lng" name="lng" class="form-control form-control-sm" placeholder="lng" v-model="vdata.lng" >
                                                    </div>
                                                </div>
                                            </div>
                                            <button type="button"  class="btn btn-sm btn-dark float-right " @click="gantiPosisi"><span class="typcn typcn-arrow-repeat"></span></button>
                                            <p class="text-sm">Type Popup : </p>
                                            <input type="radio" id="click" value="click" v-model="vdata.popup">
                                            <label for="click">click</label>
                                            <input type="radio" class="ml-2" id="hover" value="hover" v-model="vdata.popup">
                                            <label for="hover">hover</label>
                                            <!-- ALL -->
                                            <hr>
                                            <div v-if="vdata.id">
                                            <button type="button" class="btn btn-sm btn-style10  " @click="$router.push(`/gis/zadmin/kategori/event/event1?eventid=${vdata.event}`)">Event 1</button>
                                            <p class="text-sm">Aktif ? : </p>
                                            <select class='form-control' v-model="vdata.event" style="width:200px;">
                                                <option>false</option>
                                                <option>true</option>
                                            </select>
                                                <label for="query">Query dipake</label>
                                                <select class='form-control' v-model="vdata.idquery" @change="ubahquery">
                                                    <option v-for="(item, index) in pilihan" :key="index+'pilhan'" :value="item.id">{{item.title}}</option>
                                                </select>
                                                <div class="sm-form ">
                                                    <input type="text" id="query" name="query" class="form-control form-control-sm" placeholder="query * from tbuser" v-model="vdata.query"  >
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-sm-5">
                                            <!-- RADIUS -->
                                            <div v-if="pilihkategori=='radius'">
                                                <div class="sm-form ">
                                                    <label class="p-0 mt-2" for="radius">Radius lingkaran/area : </label>
                                                    <p class="text-xs text-red-300">per Meter/M</p>
                                                    <input required type="text" id="radius" style="width:50%;" name="radius" class="form-control form-control-sm" placeholder="ex: 100" v-model="vdata.radius" >
                                                </div>
                                                <div class="sm-form ">
                                                    <label for="color">Warna/color : </label>
                                                    <input required type="color" id="color" name="color" class="form-control form-control-sm" placeholder="color" v-model="vdata.color" >
                                                </div>
                                                <div class="sm-form ">
                                                    <label for="opacity">Opacity (seberapa transparent) : </label>
                                                    <input required type="text" id="opacity" name="opacity" class="form-control form-control-sm" placeholder="ex : 0.0 - 1.0" v-model="vdata.opacity" >
                                                </div>
                                            </div>
                                            <!-- RADIUS -->
                                            <div v-else>
                                                <div v-if="pilihkategori=='icon'">
                                                    <label for="file" class="p-0 mt-2">Icon Link/Upload: </label>
                                                    <input  type="file" id="filenya" name="file" class="form-control form-control-sm" placeholder="file" @change="upload" >
                                                    <input required type="text" id="icon" name="icon" class="form-control form-control-sm" placeholder="icon" v-model="vdata.icon" >
                                                    <button type="button" class="btn btn-sm btn-dark  " @click="defaults"><span class="typcn typcn-arrow-loop"></span></button>
                                                    <div v-if="defaultOn">
                                                        <div class="sm-form ">
                                                            <label for="color">Pin Option</label>
                                                            <input type="color" id="color" name="color" class="form-control form-control-sm" placeholder="color" v-model="pin.color" @change="defaults">
                                                            <input type="text" id="type" name="type" class="form-control form-control-sm" placeholder="type" v-model="pin.type" @change="defaults">
                                                        </div>
                                                    </div>
                                                </div>
                                                <br>
                                                <div>
                                                    <label for="file" class="p-0 mt-2">Gambar Link/Upload: </label>
                                                    <p class="text-xs text-red-500" v-if="pilihkategori=='icon'">Gambar akan berada di deskripsi saat icon di klik </p>
                                                    <input  type="file" id="filenya2" name="file" class="form-control form-control-sm" placeholder="file" @change="upload2" >
                                                    <input  type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                               <!-- BUTTON PART -->
                                <div class="mt-3 p-3">
                                    <button
                                    type="submit"
                                    class="btn btn-sm"
                                    :class="
                                        btn == 'tambah'
                                        ? 'btn-primary'
                                        : btn == 'update'
                                        ? 'btn-success'
                                        : 'btn-danger'
                                    "
                                    >
                                    <span class="typcn typcn-info"></span>  Proses {{ btn }}
                                    </button>
                                    <div class="float-right">
                                    <button
                                        type="button"
                                        v-show="btn!='tambah'"
                                        @click="
                                        btn = 'tambah';
                                        vdata = {};
                                        "
                                        class="btn btn-sm btn-dark"
                                    >
                                        <span class="typcn typcn-edit"></span> Tambah
                                    </button>
                                    <button
                                        type="button"
                                        @click="btn = 'delete'"
                                        v-show="btn!='tambah'"
                                        class="ml-3 btn btn-sm btn-danger"
                                    >
                                        <span class="typcn typcn-delete"></span> Delete
                                    </button>
                                    </div>
                                </div>
                                <!-- END BUTTON PART -->
                            </form>
                             <!-- TABLE PART -->
                            <div v-if="datanya.length>0" class="p-3">
                            <Btables :tables="td"  :cari="['']" :hide="['id','wilayah','id_peta','deskripsi','icon','gambar','radius','color','opacity','floorplan','query','query2','height','width','nilai1','nilai2','nilai3','nilai4','center1','center2','idquery']" @selected="ambil" ref="btable" />
                            </div>
                            <!-- END TABLE PART -->
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div v-if="ready">
            <div id="mapid" style="height:100vh;width:100vw;background:white;">
                
            </div>
        </div>
    </div>
</template>
<script>
// import FreeDraw from 'leaflet-freedraw';
import autophp from "@/plugins/autophp_laravel.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
let urlnya=localStorage.getItem('urlnya');
let map;
var featureGroup
export default{
    components:{
     Btables,
    },
    data(){
        return{
            photoSize:[-30.8650, 155.2094], // penting  [height, width] , more minus height = more height
            defaultOn:false,
            modal:false,
            setting:false,
            modes:false,
            selected: false,
            ready:false,
            mode:'free',
            vdata2:{
                title:'',
                color:'#f00f0f',
            },
            center:[-2.3615836073925434, 115.46956926581515],
            vdata: {
                opacity:'0.6',
                radius:'100',
                height:-30.8650,
                width:155.2094, // penting  [height, width] , more minus height = more height
            },
            pin:{
                color:'#f00f0f',
                type:'marker',
            },
            btn: "tambah",
            wilayah:[],
            pilihan:[],
            datanya:[],
            posisi:[],
            nilai:[],
            pilihkategori:'icon',
            iconSize:30,
        }
    },
    computed:{
        td(){
            let that=this;
            let data=this.datanya
            data=data.filter(e=>{
                if(e.kategori==that.pilihkategori){
                    return e
                }
            })
            return data
        },
        dynamicSize() {
            return [this.iconSize, this.iconSize * 1.15];
        },
            dynamicAnchor() {
            return [this.iconSize / 2, this.iconSize * 1.15];
        }
    },
    watch:{
        modes(){
            this.ready=false
            this.getWilayah();
            this.$forceUpdate();
        }
    },
    mounted() {
        let that=this;
        if(this.$route.query.geo){
            if(this.$route.query.geo=='true'){
                setTimeout(() => {
                    document.querySelector('#modesnya').click();
                }, 1000);
            }
        }
        this.getWilayah();
        this.getDataRoom();
        
    },
    methods: {
        ubahquery(){
            this.vdata.query=`select * from app_gis_ruangan where id='${this.vdata.idquery}' order by id`
            // this.vdata.query=`select id,gambar,title,status,fasilitas,detail from app_gis_ruangan where id='${this.vdata.idquery}' order by id`
            this.$forceUpdate();
        },
        getDataRoom(){
            sdb.collection("app_gis_ruangan",false,urlnya).doc().select(`select * from app_gis_ruangan where idpeta='${this.$route.query.id}'`).then(res => {
                console.log(res);
                this.pilihan=res;
                this.$forceUpdate();
            });
            
        },
        events(event){
            alert(event)
        },
        exporta(){
            let that=this;
            var data = featureGroup.toGeoJSON();
            data.features.forEach(e=>{
                e.properties.stroke=that.vdata2.color;
                e.properties['stroke-width']=5;
                e.properties.title=that.vdata2.title;
            })
            var convertedData = 'text/json;charset=utf-8,' + encodeURIComponent(JSON.stringify(data));
            // Create export
            document.getElementById('export').setAttribute('href', 'data:' + convertedData);
            document.getElementById('export').setAttribute('download','data.geojson');
        },
        defaults(){
            this.defaultOn=true;
            var color = this.pin.color.replace('#','')
            this.vdata.icon = `https://a.tiles.mapbox.com/v4/marker/pin-m-${this.pin.type}+${color}@2x.png?access_token=pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4NXFhYTA2bTMyeW44ZG0ybXBkMHkifQ.gUGbDOPUN1v1fTs5SeOR4A`
        },
        getWilayah(){
            let that=this;
            sdb.collection("app_gis_wilayah_balangan",false,urlnya).doc().select(`select * from app_gis_wilayah_balangan`).then(res => {
                that.wilayah=res;
                console.log('wilayah',res)
                sdb.collection("app_gis_marker",false,urlnya).doc().select(`select floorplan,height,width,center1,center2,nilai1,nilai2,nilai3,nilai4
                 from app_gis_marker where id_peta='${that.$route.query.id}'`).then(res => {
                    console.log(res)
                    if(res.length>0){
                        that.vdata.height=parseFloat(res[0].height)
                        that.vdata.width=parseFloat(res[0].width)
                        that.nilai[0]=parseFloat(res[0].nilai1);
                        that.nilai[1]=parseFloat(res[0].nilai2);
                        that.nilai[2]=parseFloat(res[0].nilai3);
                        that.nilai[3]=parseFloat(res[0].nilai4);
                        if(res[0].center1){
                            that.center=[parseFloat(res[0].center1),parseFloat(res[0].center2)]
                        }
                        that.vdata.floorplan=res[0].floorplan
                        that.photoSize=[parseFloat(res[0].height),parseFloat(res[0].width)]
                        console.log(that.photoSize)
                        that.$forceUpdate();
                    }
                    that.loadmap()
                });
            });
        },
        loadmap(){
            let that=this;
            this.getData();
            this.ready=false;
            map=null;
            setTimeout(() => {
                this.ready=true;
                setTimeout(() => {
                    // =======================================
                    // map = new L.Map('mapid');
                    // map.setView([ -2.3662494, 115.4641079], 10);
                    // L.tileLayer('http://{s}.google.com/vt/lyrs=m&x={x}&y={y}&z={z}',{
                    //     maxZoom: 20,
                    //     subdomains:['mt0','mt1','mt2','mt3']
                    //     }).addTo(map);
                    // map.on('click',function(e){
                    //     console.log(e)
                    //     that.posisi[0]=e.latlng.lat
                    //     that.posisi[1]=e.latlng.lng
                        
                    //     that.modal=true;
                    //     that.$forceUpdate();
                    // })      
                    // ===========================================
                    
                    // map.setView([-32.8650, 153.2094], 8);
                    map = new L.Map('mapid');
                    var center=that.center; // memposisi kan 
                    map.setView(center, 19);
                    // L.tileLayer('',{
                    // L.tileLayer('http://{s}.google.com/vt/lyrs=m&x={x}&y={y}&z={z}',{
                    //     maxZoom: 25,
                    //     subdomains:['mt0','mt1','mt2','mt3']
                    // })
                    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                        maxZoom: 19,
                        attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    })
                    .addTo(map);
                    if(!this.modes){
                        map.on('click',function(e){
                            console.log(e)
                            that.posisi[0]=e.latlng.lat
                            that.posisi[1]=e.latlng.lng
                            
                            that.modal=true;
                            that.$forceUpdate();
                        })
                    }
                    if(this.vdata.floorplan){

                        var imageUrl = that.vdata.floorplan,
                    // [-1<>+10,+9<>-5]
                    imageBounds = [[center[0]-that.nilai[0],center[1]+that.nilai[1]], [center[0]+that.nilai[2],center[1]-that.nilai[3]]]; // center, [height, width] , more minus height = more height
                    L.imageOverlay(imageUrl, imageBounds).addTo(map);                
                    }
                    // ======================================
                    
                    featureGroup = L.featureGroup().addTo(map);

                    var drawControl = new L.Control.Draw({
                        edit: {
                            featureGroup: featureGroup
                        }
                    }).addTo(map);
                    
                     map.on(L.Draw.Event.CREATED, function (e) {
                         e.layer.properties={
                             stroke:that.color
                         }
                         console.log(e.layer)
                        // e.layer.options.color = that.color;
                        var type = e.layerType,
                            layer = e.layer;
                        console.log('layer',layer)
                        featureGroup.addLayer(e.layer);
                        if (type === 'marker') {
                        }
                        map.addLayer(layer);
                        alert('layer added')
                    });
                    // map.on('draw:created', function (e) {
                    //     var layers = e.layers;
                    //     console.log(layers)
                    // });
                    if(that.datanya.length>0){
                    that.datanya.forEach(e=>{
                        console.log('kategori',e.kategori)
                    // =================================================================
                        if(e.kategori=='icon'){
                            var greenIcon = L.icon({
                                iconUrl: e.icon,
                                iconSize:     [28, 75], // size of the icon
                                iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                                popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                            });
                            if(e.popup=='click'){
                                L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                    ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                                </div>
                                `, {maxWidth: 500}).addTo(map)
                                .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                })
                            }else{
                                L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                    ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                                </div>
                                `,{ permanent: false, sticky: true }).addTo(map)
                                 .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                })
                            }
                    // =================================================================
                }else if(e.kategori=='barcode'){
                    console.log('kategori',e)
                    var greenIcon = L.icon({
                        iconUrl: "https://gis.egov.balangankab.go.id/files/me.png",
                        iconSize:     [28, 75], // size of the icon
                        iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                        popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                    });
                    if(e.popup=='click'){
                        L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.idbarcode}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `, {maxWidth: 500}).addTo(map)
                        .addEventListener('click',function(){
                            if(e.event!='false' && e.event){
                                that.events(e.event)
                            }
                        })
                    }else{
                        L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.idbarcode}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `,{ permanent: false, sticky: true }).addTo(map)
                            .addEventListener('click',function(){
                            if(e.event!='false' && e.event){
                                that.events(e.event)
                            }
                        })
                    }
            // =================================================================
                }else if(e.kategori=='gambar'){
                            var greenIcon = L.icon({
                                iconUrl: e.gambar,
                                iconSize:     [48, 45], // size of the icon
                                iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                                popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                            });
                            if(e.popup=='click'){
                                L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                    ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                                </div>
                                `, {maxWidth: 500}).addTo(map)
                                 .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                })
                            }else{
                                L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                    ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                                </div>
                                `,{ permanent: false, sticky: true }).addTo(map)
                                 .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                })
                            }
                    // =================================================================
                        }else if(e.kategori=='radius'){
                            if(e.popup=='click'){
                                L.circle([e.lat, e.lng], {
                                    color: "black",
                                    fillColor: e.color,
                                    fillOpacity: e.opacity,
                                    radius: e.radius
                                }).bindPopup(`
                                <div class="text-xs">
                                    <div class="font-bold uppercase">${e.title}</div>
                                    <div class="text-justify">${e.deskripsi}</div>
                                </div>
                                `, {maxWidth: 500}).addTo(map)
                                 .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                });
                            }else{
                                L.circle([e.lat, e.lng], {
                                    color: "black",
                                    fillColor: e.color,
                                    fillOpacity: e.opacity,
                                    radius: e.radius
                                }).bindTooltip(`
                            <div class="text-xs">
                                <div class="font-bold uppercase">${e.title}</div>
                                <div class="text-justify">${e.deskripsi}</div>
                            </div>
                            `,{ permanent: false, sticky: true }).addTo(map)
                             .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                });
                            }
                        }
                // =================================================================
                    })
                    }
                  L.control.ruler().addTo(map);
                }, 2000);
                
            }, 1000);
        },
        gantiPosisi(){
            this.vdata.lat=this.posisi[0];
            this.vdata.lng=this.posisi[1];
            this.$forceUpdate();
        },
        getData(){
                sdb.collection("app_gis_marker",false,urlnya).doc().select(`
                select *,tb1.id as id
                 from app_gis_marker tb1 left join app_gis_wilayah_balangan tb2 on tb2.id=tb1.wilayah where tb1.id_peta='${this.$route.query.id}'`).then(res => {
                    console.log('hasil',res)
                    this.datanya=res;
                    this.$forceUpdate();
                });
            },
        ambil(data) {
            this.vdata = data;
            this.pilihkategori=data.kategori
            this.btn = "update";
            this.$forceUpdate();
        },
        submit() {
            let that=this
            that.vdata.kategori=that.pilihkategori;
            that.vdata.id_peta=this.$route.query.id
            if (confirm("Apakah yakin proses dilanjutkan ?")) {
                if (this.btn == "tambah") {
                //idnya letak di vdata
                delete this.vdata.id;
                sdb
                    .collection("app_gis_marker",false,urlnya)
                    .doc()
                    .set(this.vdata)
                    .then((res) => {
                    that.getData();
                    // that.$nuxt.$emit('getData')

                    });
                } else if (this.btn == "update") {
                sdb
                    .collection("app_gis_marker",false,urlnya)
                    .doc()
                    .set(this.vdata)
                    .then((res) => {
                    that.getData();
                    // that.$nuxt.$emit('getData')
                    });
                } else if (this.btn == "delete") {
                    console.log(this.vdata)
                sdb
                    .collection("app_gis_marker",false,urlnya)
                    .doc(this.vdata.id)
                    .delete()
                    .then((res) => {
                    that.getData();
                    // that.$nuxt.$emit('getData')
                    });
                }
                // this.$nuxt.$emit('getData','ok')
                this.loadmap()
            }
        },
        upload(){
            let file = document.querySelector("#filenya"); // berikan id pada input file
            sdb.collection('tbuser',false,urlnya).upload(file).then(res=>{
                console.log(res)
                this.vdata.icon=res;
                this.$forceUpdate();
            })
        },
        upload2(){
            let file = document.querySelector("#filenya2"); // berikan id pada input file
            sdb.collection('tbuser',false,urlnya).upload(file).then(res=>{
                console.log(res)
                this.vdata.gambar=res;
                this.$forceUpdate();
            })
        },
        upload3(){
            let file = document.querySelector("#filenya3"); // berikan id pada input file
            sdb.collection('tbuser',false,urlnya).upload(file).then(res=>{
                console.log(res)
                this.vdata.floorplan=res;
                this.$forceUpdate();
            })
        }
    },
     metaInfo: {
